.empForm{
    width:400px;
    margin: 10px auto;
    background-color: #f0f8ff;
    border-radius: 5px;
    padding:15px;
}

.empForm tr input{
    width:355px;
    height:25px;
    border:1px solid gray;
    margin-top:5px;
    padding-left:10px;
    border-radius: 5px;
}

button{
    color:white;
    background-color: #4267b2;
    padding:5px 15px;
    border:none;
    display: block;
    margin:10px auto 0px auto;
    border-radius:5px;  
}

h3{
    margin:0px;
    color:#4267b2;
    text-align: center;
}